<template>
  <tas-base-crud ref="sub-module" :config="config" hide-update hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="openModal('modal-rincian-iuran', data)" class="btn btn-info btn-sm">Detail Rincian </a>
    </template>
    <template v-slot:list-header>
      <b-modal header-class="pb-1" v-if="modalData" id="modal-rincian-iuran" hide-footer size="xl">
        <template #modal-header>
          <div class="d-flex justify-content-between flex-fill">
            <div class="d-flex flex-column flex-fill">
              <h5 class="mb-3">Detail Rincian Iuran Simpan Pinjam - Bulan {{ modalData.thisMonth.periode_month | parse('period') }}</h5>
              <!-- <span>{{ modalData.rel_employee_id }} - <span :class="modalData.status_code === 'calculated'? 'text-success' : 'text-danger' " class="font-weight-bold">{{ modalData.status_code | parse('calculated') }}</span></span> -->
              <table class="table no-border mb-0">
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Tenaga Kerja &emsp;</td>
                  <td class="pt-0 pr-0 nowrap-table">{{ modalData.rel_employee_id }}</td>
                  <td rowspan="4" class="pr-10 pl-0">
                    <!-- <div class="form-group mb-0 mt-0">
                      <textarea v-model="modalData.thisMonth.description" class="form-control" rows="4" placeholder="Deskripsi / note Rincian iuran simpan pinjam"></textarea>
                    </div> -->
                  </td>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Total Simpanan</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">Rp. </span>
                      <span class="text-right font-weight-bold">{{ modalData.simpanan_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Total Pinjaman</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">Rp. </span>
                      <span class="text-right font-weight-bold">{{ modalData.pinjaman_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
                <tr>
                  <td class="pt-0 pl-0 nowrap-table">Total</td>
                  <th class="pt-0 pr-0 nowrap-table">
                    <div class="d-flex justify-content-between">
                      <span class="mr-4">Rp. </span>
                      <span class="text-right font-weight-bold">{{ modalData.total_value  || 0 | parse('number') }}</span>
                    </div>
                  </th>
                </tr>
              </table>
            </div>
          </div>
        </template>
        <div class="row">
          <div v-if="modalData.beforeMonth" class="col-5 order-2">
            <h5 class="mb-4">Detail Rincian bulan sebelumnya ({{ modalData.beforeMonth.periode_month | parse('period') }})</h5>
            <div class="in-card-border border-primary">
              <h5 class="font-weight-bolder text-center mb-3">Simpanan</h5>
              <template v-if="modalData.beforeMonth.child_koperasi_simpanan_details.length">
                <table class="table no-border font-size-sm mb-0">
                  <tr v-for="(m,i) in modalData.beforeMonth.child_koperasi_simpanan_details" :key="i+'-tr-simpanan-before'">
                    <td class="pt-0 pl-0 font-size-sm">{{ m.rel_comp_simpanan_koperasi_id }}</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ m.comp_value || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td style="border-top: 2px solid" class="pl-0 font-size-sm font-weight-bolder text-primary border-primary">Total Simpanan</td>
                    <th style="border-top: 2px solid" class="pr-0 font-size-sm border-primary">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{  modalData.beforeMonth.simpanan_value || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                </table>
                <a @click="copyValue('child_koperasi_simpanan_details')" class="btn btn-block btn-light-primary mt-3"><i class="ri-file-copy-line"></i> Salin nilai simpanan </a>
                <!-- <p class="text-left mb-0 mt-3 font-size-xs font-style-italic">Note: <br> {{ modalData.beforeMonth.description || '-' }}</p> -->
              </template>
              <div v-else class="alert alert-custom alert-light-danger mt-5" role="alert">
                <div class="alert-icon">
                  <span class="svg-icon svg-icon-danger svg-icon-xl">
                    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect x="0" y="0" width="24" height="24" />
                        <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                        <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                        <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                      </g>
                    </svg>
                  </span>
                </div>
                <div class="alert-text">Data Simpanan pada bulan lalu belum dibuat atau tidak ditemukan!</div>
              </div>
            </div>
            <div v-if="modalData.beforeMonth" class="in-card-border border-primary mt-3">
              <h5 class="font-weight-bolder text-center mb-3">Pinjaman</h5>
              <template v-if="modalData.beforeMonth.child_koperasi_pinjaman_details.length">
                <table class="table no-border font-size-sm mb-0">
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Kali Pembayaran</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class=""></span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].count_pembayaran || 0 | parse('number') }}x</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Sisa Angsuran</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class=""></span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].sisa_angsuran || 0 | parse('number') }}x</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Angsuran ke</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class=""></span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].angsuran_ke || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Pembayaran Angsuran</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class=""></span>
                        <span class="text-right font-weight-bolder text-uppercase">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].ada_angsuran | parse('ynBool') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Nilai Pinjaman </td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].pinjaman_value || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Pokok Pinjaman </td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].pokok_pinjaman_value || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td class="pt-0 pl-0 font-size-sm">Bunga Pinjaman (<span class="font-weight-bolder"> {{modalData.beforeMonth.child_koperasi_pinjaman_details[0].prosentase_bunga }}%</span>)</td>
                    <th class="pt-0 pr-0 font-size-sm">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].bunga_pinjaman_value || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                  <tr>
                    <td style="border-top: 2px solid" class="pl-0 font-size-sm font-weight-bolder text-primary border-primary">Jumlah Angsuran</td>
                    <th style="border-top: 2px solid" class="pr-0 font-size-sm border-primary">
                      <div class="d-flex justify-content-between">
                        <span class="">Rp.</span>
                        <span class="text-right font-weight-bold">{{  (modalData.beforeMonth.child_koperasi_pinjaman_details[0].pokok_pinjaman_value + modalData.beforeMonth.child_koperasi_pinjaman_details[0].bunga_pinjaman_value) || 0 | parse('number') }}</span>
                      </div>
                    </th>
                  </tr>
                </table>
                <p class="text-left mb-0 mt-3 font-size-xs font-style-italic">Note: <br> {{ modalData.beforeMonth.child_koperasi_pinjaman_details[0].description || '-' }}</p>
                <a @click="copyValue('child_koperasi_pinjaman_details')" class="btn btn-block btn-light-primary mt-5"><i class="ri-repeat-line"></i> Lanjutkan Pinjaman </a>
              </template>
              <div v-else class="alert alert-custom alert-light-danger mt-5" role="alert">
                <div class="alert-icon">
                  <span class="svg-icon svg-icon-danger svg-icon-xl">
                    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect x="0" y="0" width="24" height="24" />
                        <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                        <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                        <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                      </g>
                    </svg>
                  </span>
                </div>
                <div class="alert-text">Data Pinjaman pada bulan lalu belum dibuat atau tidak ditemukan!</div>
              </div>
            </div>
          </div>
          <div v-if="modalData.thisMonth" :class="modalData.beforeMonth? 'col-7 border-right': 'col-12'">
            <h5 class="font-weight-bolder mb-3">Deskripsi Rincian Iuran Simpan Pinjam</h5>
            <div class="form-group mb-3 mt-0">
              <textarea v-model="modalData.thisMonth.description" class="form-control" rows="4" placeholder="Deskripsi / note Rincian iuran simpan pinjam"></textarea>
            </div>
            <hr>
            <h5 class="font-weight-bolder mb-3">Rincian Simpanan</h5>
            <template v-for="(s,i) in modalData.thisMonth.child_koperasi_simpanan_details">
              <s-input :key="i+'-form-simpanan'" v-model="s.comp_value" :item="{ type:'number', validation:[], label:s.rel_comp_simpanan_koperasi_id, prefix: 'Rp.', groupClass:'mb-3', ic:'mr-4', api:'', attr: { precision:2 } }" />
            </template>
            <hr>
            <h5 class="font-weight-bolder mb-3">Rincian Pinjaman</h5>
            <template v-if="modalData.thisMonth.child_koperasi_pinjaman_details.length">
              <s-input @input="countPokok()" v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].pinjaman_value" :item="{ type:'number', validation:[], label:'Nilai Pinjaman', prefix: 'Rp.', groupClass:'mb-3', ic:'mr-4', api:'', attr: { precision:2 } }" />
              <s-input v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].description" :item="{ type:'textarea', validation:[], label:'Deskripsi / Note', api:'', rows:1 }" />
              <div class="form-group mt-5">
                <label>Pembayaran Angsuran</label>
                <div class="checkbox-list">
                  <label class="checkbox">
                    <input type="checkbox" @change="resetPinjaman(modalData.thisMonth.child_koperasi_pinjaman_details[0].ada_angsuran)" name="ada-angsuran-pembayaran" v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].ada_angsuran">
                    <span></span>Checklist apabila ada pembayaran angsuran bulan ini</label>
                </div>
              </div>
              <template v-if="modalData.thisMonth.child_koperasi_pinjaman_details[0].ada_angsuran">
                <s-input @input="countSisa()" v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].count_pembayaran" :item="{ type:'number', validation:[], label:'Kali Pembayaran', groupClass:'mb-5', api:'' }" />
                <s-input @input="countSisa()" v-model.lazy="modalData.thisMonth.child_koperasi_pinjaman_details[0].angsuran_ke" :item="{ type:'number', validation:[], label:'Angsuran Ke', groupClass:'mb-5', api:'' }" />
                <s-input v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].sisa_angsuran" :item="{ type:'number', validation:[], label:'Sisa Angsuran', suffix: 'Kali', groupClass:'mb-5', ic:'ml-4', api:'' }" />
                <s-input @input="countBunga()" v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].prosentase_bunga" :item="{ type:'number', validation:[], label: 'Prosen Bunga Pinjaman (%)', groupClass:'mb-5', ic:'mr-4', api:'' }" />
                <s-input v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].pokok_pinjaman_value" :item="{ type:'number', validation:[], label:'Nilai Pokok Pinjaman', prefix: 'Rp.', groupClass:'mb-3', ic:'mr-4', api:'', attr: { precision:2 } }" />
                <s-input v-model="modalData.thisMonth.child_koperasi_pinjaman_details[0].bunga_pinjaman_value" :item="{ type:'number', validation:[], label:'Nilai Bunga Pinjaman', prefix: 'Rp.', groupClass:'mb-2', ic:'mr-4', api:'', attr: { precision:2 } }" />
              </template>
            </template>
          </div>
        </div>
        <div class="mt-5">
          <button @click="submitData('modal-rincian-iuran')" class="btn btn-primary btn-lg btn-block font-size-md">Perbarui &amp; Simpan</button>
        </div>
      </b-modal>
    </template>
  </tas-base-crud>
</template>

<script>

export default {
  name: 'crud-koperasi-simpan-pinjam-details',
  data () {
    return {
      mainKey: 1,
      modalData: {},
      config: {
        title: 'Rincian Iuran Simpan Pinjam',
        model_api: 'koperasi-simpan-pinjam-details',
        getter: 'koperasi-simpan-pinjam-details',
        setter: 'koperasi-simpan-pinjam-details',
        pk_field: null,
        permission: {
          create: 'create-koperasi-simpan-pinjam-details',
          read: 'view-koperasi-simpan-pinjam-details',
          update: 'update-koperasi-simpan-pinjam-details',
          delete: 'delete-koperasi-simpan-pinjam-details'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'koperasi_simpan_pinjam_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'simpanan_value', label: 'Jumlah Simpanan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'pinjaman_value', label: 'Jumlah Pinjaman', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          { id: 'total_value', label: 'Total', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'status_code',
            data: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_code',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_code',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        // console.log(this.$refs['sub-module'])
        this.$refs['sub-module'].$children[2].getData()
      }
    }
  },
  methods: {
    async openModal (id, data) {
      const detail = {
        thisMonth: await this.getDetail('custom/koperasi-simpan-pinjam-details/show-with-detail', { id: data.rowData.id }),
        beforeMonth: await this.getDetail('custom/koperasi-simpan-pinjam-details/show-month-before', { id: data.rowData.id })
      }
      this.modalData = Object.assign({}, detail, data.rowData)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    getDetail (api, param, obj = null) {
      return this.$_api.get(api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    copyValue (target) {
      let from = JSON.parse(JSON.stringify(this.modalData.beforeMonth[target]))
      let to = JSON.parse(JSON.stringify(this.modalData.thisMonth[target]))
      console.log(from, to)
      for (let i = 0; i < from.length; i++) {
        let el = from[i]
        delete el.koperasi_simpan_pinjam_detail_id
        delete el.updated_at
        delete el.updated_by
        delete el.created_at
        delete el.created_by
        if (target === 'child_koperasi_pinjaman_details') {
          el.angsuran_ke = el.angsuran_ke + 1
          el.sisa_angsuran = el.sisa_angsuran - 1
          el.id = to[0].id
        } else {
          let findTo = to.filter(x => x.comp_simpanan_koperasi_id === el.comp_simpanan_koperasi_id)
          if (findTo.length) el.id = findTo[0].id
        }
      }
      const result = from.filter(x => to.map(y => y.id).includes(x.id))
      this.$set(this.modalData.thisMonth, target, result)
    },
    submitData (modalID) {
      const api = 'custom/koperasi-simpan-pinjam-details/create-with-detail'
      this.$_api.post(api, this.modalData.thisMonth).then(res => {
        this.$set(this.modalData, 'simpanan_value', Number(res.data.simpanan_value))
        this.$set(this.modalData, 'pinjaman_value', Number(res.data.pinjaman_value))
        this.$set(this.modalData, 'total_value', Number(res.data.total_value))
        this.mainKey++
        this.$_alert.success(null, 'Detail Rincian berhasil diperbarui')
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    resetPinjaman (value) {
      if (!value) {
        const init = {
          count_pembayaran: 0,
          angsuran_ke: 0,
          sisa_angsuran: 0,
          prosentase_bunga: 0,
          pokok_pinjaman_value: 0,
          bunga_pinjaman_value: 0
        }
        this.modalData.thisMonth.child_koperasi_pinjaman_details[0] = Object.assign(this.modalData.thisMonth.child_koperasi_pinjaman_details[0], init)
      }
    },
    countSisa () {
      const value = this.modalData.thisMonth.child_koperasi_pinjaman_details[0].count_pembayaran - this.modalData.thisMonth.child_koperasi_pinjaman_details[0].angsuran_ke
      this.$set(this.modalData.thisMonth.child_koperasi_pinjaman_details[0], 'sisa_angsuran', value)
      this.countPokok()
    },
    countPokok () {
      const value = (this.modalData.thisMonth.child_koperasi_pinjaman_details[0].pinjaman_value / (this.modalData.thisMonth.child_koperasi_pinjaman_details[0].count_pembayaran || 1))
      this.$set(this.modalData.thisMonth.child_koperasi_pinjaman_details[0], 'pokok_pinjaman_value', value)
      this.countBunga()
    },
    countBunga () {
      const value = (this.modalData.thisMonth.child_koperasi_pinjaman_details[0].prosentase_bunga / 100) * this.modalData.thisMonth.child_koperasi_pinjaman_details[0].pinjaman_value
      this.$set(this.modalData.thisMonth.child_koperasi_pinjaman_details[0], 'bunga_pinjaman_value', value)
    }
  }
}
</script>
<style scoped>
  .in-card-border {
    border: 2px solid;
    border-radius: 8px;
    padding: 1rem;
  }
</style>



